
*{
  margin: 0;
  padding: 0;
}
body {
  overflow-x: hidden!important;
}
.slide-left-enter,
.slide-right-leave-active {
  -webkit-transform: translate(800px, 0);
          transform: translate(800px, 0);
  transition: 0.4s;
}
.slide-left-leave-active,
.slide-right-enter {
  -webkit-transform: translate(-800px, 0);
          transform: translate(-800px, 0);
  transition: 0.4s;
}
.layout .block {
  height: 68px;
}
.layout .main-content {
  max-width: 600px;
  min-height: calc(100vh-60px);
  margin: 0 auto;
}
.layout .menu {
  box-shadow: 0 3px 6px #aaa;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}
.layout .menu-title {
  font-size: 24px;
}



