
.list-item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    max-height: 100px;
    border-bottom: 1px solid #dfdfdf;
    padding-bottom: 8px;
    padding-top: 8px;
    font-family: '微软雅黑';
    margin-left: 10px;
    margin-right: 10px;
}
.list-item .picture {
    width: 100px;
    height: 100px;
    margin-right: 5px;
}
.list-item .title-block {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin-left: 5px;
}
.list-item .title-block .main-title {
    margin: 0;
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
}
.list-item .title-block .sub-title {
    -webkit-box-flex: 5;
        -ms-flex: 5;
            flex: 5;
    margin: 0;
    margin-top: 5px;
    margin-left: 5px;
    color: #999;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    letter-spacing: 2px;
    font-size: 14px;
}
.list-item.visited .main-title {
    color: #999;
}
.list-item.visited .sub-title {
    color: #bbb;
}



